/*REACT GROUP*/
.fadedown-enter {
  -webkit-transform: translateY(100%);
          transform: translateY(100%); }

.fadedown-enter.fadedown-enter-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  transition: -webkit-transform 2500ms cubic-bezier(1, 0.05, 0.25, 1);
  transition: transform 2500ms cubic-bezier(1, 0.05, 0.25, 1);
  transition: transform 2500ms cubic-bezier(1, 0.05, 0.25, 1), -webkit-transform 2500ms cubic-bezier(1, 0.05, 0.25, 1); }

.fadedown-exit {
  -webkit-transform: translateY(0);
          transform: translateY(0); }

.fadedown-exit.fadedown-exit-active {
