  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
  transition: opacity 700ms ease-out, -webkit-transform 700ms ease-out;
  transition: transform 700ms ease-out, opacity 700ms ease-out;
  transition: transform 700ms ease-out, opacity 700ms ease-out, -webkit-transform 700ms ease-out; }

.fade-enter-done {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1; }

.arrows {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 500px;
  position: absolute;
  bottom: 20px; }

.chevron {
  position: absolute;
  width: 3.5rem;
  height: 0.8rem;
  opacity: 0;
  -webkit-transform: scale(0.3);
          transform: scale(0.3);
  -webkit-animation: move-chevron 3s ease-out infinite;
          animation: move-chevron 3s ease-out infinite; }

.chevron:first-child {
  -webkit-animation: move-chevron 3s ease-out 1s infinite;
          animation: move-chevron 3s ease-out 1s infinite; }

.chevron:nth-child(2) {
  -webkit-animation: move-chevron 3s ease-out 2s infinite;
          animation: move-chevron 3s ease-out 2s infinite; }

.chevron:before,
.chevron:after {
  content: '';
  position: absolute;
  top: 0;
  height: 100%;
  width: 50%;
  background: #ffffff; }

.chevron:before {
  left: 0;
  -webkit-transform: skewY(30deg);
          transform: skewY(30deg); }

.chevron:after {
  right: 0;
  width: 50%;
  -webkit-transform: skewY(-30deg);
          transform: skewY(-30deg); }

@-webkit-keyframes move-chevron {
  25% {
    opacity: 1; }
  33.3% {
    opacity: 1;
    -webkit-transform: translateY(3.8rem);
            transform: translateY(3.8rem); }
  66.6% {
    opacity: 1;
    -webkit-transform: translateY(5.2rem);
            transform: translateY(5.2rem); }
  100% {
    opacity: 0;
    -webkit-transform: translateY(8rem) scale(0.5);
            transform: translateY(8rem) scale(0.5); } }

@keyframes move-chevron {
  25% {
    opacity: 1; }
  33.3% {
    opacity: 1;
    -webkit-transform: translateY(3.8rem);
            transform: translateY(3.8rem); }
  66.6% {
    opacity: 1;
    -webkit-transform: translateY(5.2rem);
            transform: translateY(5.2rem); }
  100% {
    opacity: 0;
    -webkit-transform: translateY(8rem) scale(0.5);
            transform: translateY(8rem) scale(0.5); } }

